body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  background-color: #21232a;
  text-align: center;
  height: 100%;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

h2 {

    font-size: 1.9rem;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

input {

  color: lightgrey;
  background-color: #21232a;
  width: 85%;
  float: center;
  border-radius: 25px;
  font-size: 1.2rem;
  padding: 3px;
  padding-left: 10px;
  padding-right: 10px;
  margin-top: 6px;
  border-color: white;
  outline: none;

}

.card-grid {

  display: flex;
  flex-direction: center;
  flex-wrap: wrap;

}

.card {

  align-content: center;
  border: 1px solid grey;
  border-radius: 25px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;

}

.card p {

  text-align: center;

}

.card img {

  width: 100%;

}

.titletitle {

    font-size: 2rem;

}
